.Navigation-Bar {
    z-index: 1000;
    position: fixed;
    background-color: rgba(255,255,255,0.11);
    top: 0;
    left: 0;
  
    
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
    font-family: 'Roboto', sans-serif !important;
    color: white;
  }
  
.NavButton{
    padding-inline: 8pt;
    margin-inline: 4pt;  
    height: 100%;
    cursor: pointer;    
}

.NavButton:hover{
    color: #fdd835;
}
.NavSpacer{
    min-height: 36pt;
    height:6vmin;
  }
.Spacer {
    width: 100%;
  }
  
.Logo {
    
    font-weight: 500;
  
    padding-left: 8pt;
  }
.Nav {
    display: inline;
    font-weight: 400;
  
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 8pt;
}

.Nav-Size{
    min-height: 36pt;
    height: 6vmin;
    min-width: 100%;
}

a:visited{
  text-decoration: none;
  color: #fff;
}

a {
  text-decoration: none; /* no underline */
}