#ColorTool{
    background-color: #fff;
    /*Height is based on app's Navigation-Bar height settings*/
    height: calc(100vh - 36pt);
    max-height: calc(100vh - 6vmin);
    min-width: 100%;
    
}
/* Tool Container */
.CT-Content{
    --padding: 0vh;
    display: flex;
    flex-direction: row;
    height: calc(100% - var(--padding) * 2);
    width: auto;
    padding: var(--padding);
    background-color: rgba(0,0,0,0.16);
}


/* Image CSS */
.CT-Img{
    
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    align-content: center;
    
}

.CT-Img-Container{
    background-color: #263238;
    padding: 6vh;
}

.ImgParts{
    height: 100%;
    width: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#SVG {    
    z-index: 900;
    filter: drop-shadow(0 0 5rem rgba(0, 0, 0, 0.5));    
}

#PNG {
    z-index: 901;
    filter: drop-shadow(-1rem 1rem 3rem rgba(0, 0, 0, 0.45));    
}

#BSVG {   
    z-index: 902;    
}

#BPNG {
    z-index: 903; 
    mix-blend-mode: multiply;
}


/* Button Field CSS */
.CT-ColorPicker{
    background-color: lightgreen;
    position: relative;
    height: 100%;
    width: 520pt;
    

}

.Grid{
    display: grid;
    grid-template-columns: auto auto auto auto auto auto auto auto auto auto auto auto auto auto;
}

.Btn{
    cursor: pointer;
    display: block;
    width: 100%;
    border-radius: 0;
    background-color: #fff;
    border-color: white;
    align-content: center;
    
}
.Btn:hover{
    background-color: lightgray;
}
.BtnP {
    color: #fff;
    filter: drop-shadow(0.5px 0.5px 2px rgba(0, 0, 0, 0.6));
}

.selected {
    filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.6));
}